@media only screen and (max-width: 1275px) {
  .left-container {
    padding: 0 10px 0 10px;
  }
  .about-me-box-bottom p {
    font-size: 1.4rem;
  }
  .project-heading p {
    font-size: 2.3rem;
  }
  .project-box-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .exp-heading p {
    font-size: 2.4rem;
  }
  .contact-form-heading {
    width: 100%;
    height: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .social-media {
    width: 100%;
    display: flex;
    align-items: center;
    margin-top: 4px;
  }
  .social-media h3 {
    font-size: 3rem;
  }
  .contact-form-heading h2 {
    font-size: 2.4rem;
  }
  .contact-heading p {
    font-size: 3rem;
  }
  .contact-form-inputs {
    height: 70%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
@media only screen and (max-width: 1088px) {
  .hi {
    font-size: 2rem;
  }
  .icon-bg {
    width: 100%;
    height: 60%;
    margin-top: 120px;
  }
  .about {
    font-size: 3rem;
  }
  .about-left h1 {
    font-size: 3rem;
  }
  .exp-points {
    font-size: 1.3rem;
  }
  .contact-form-heading h2 {
    font-size: 1.5rem;
  }
  .social-media h3 {
    font-size: 3rem;
  }
}
@media only screen and (max-width: 970px) {
  .right-container {
    width: 60%;
  }
  .left-container {
    width: 40%;
  }
  .about {
    font-size: 2.7rem;
  }
  .content {
    margin-top: 50px;
  }
  .about-left h1 {
    font-size: 2rem;
  }
  .download-btn {
    font-size: 1rem;
  }
  .about-me-box-bottom p {
    font-size: 1.3rem;
  }
  .exp1 {
    height: 70%;
  }
  .exp-heading-box h6 {
    font-size: 1rem;
  }
  .exp-points {
    font-size: 1rem;
  }
  .skill-heading {
    padding: 0 30px 0 30px;
  }
}
@media only screen and (max-width: 799px) {
  .left-container {
    display: none;
  }
  .right-container {
    width: 100%;
    background-color: #ffaaa5;
    padding: 0 50px 0 50px;
  }
  .texts {
    margin-top: 1%;
    height: 78%;
  }
  .hi {
    font-size: 3rem;
    text-align: left;
  }
  .about {
    font-size: 3.5rem;
    text-align: left;
  }
  .bottomnav {
    background-color: white;
    border-bottom: 3px solid rgb(64, 64, 64);
  }
  .about-me-box-bottom p {
    font-size: 1.2rem;
  }
  .about-left {
    padding-left: 20px;
    padding-right: 20px;
  }
  .about-right {
    padding-left: 3px;
    padding-right: 17px;
  }
  .exp-box {
    display: block;
  }
  .exp-heading p {
    font-size: 2rem;
  }
  .exp1 {
    width: 100%;
    height: 40%;
    margin-top: 20px;
  }
  .contact-box1 {
    display: none;
  }
  .contact-box2 {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
  }
  .contact-heading p {
    font-size: 2rem;
  }
  .project-heading p {
    font-size: 1.77rem;
  }
  .skill-heading p {
    font-size: 2rem;
  }
}
@media only screen and (max-width: 690px) {
  .project-box-container {
    padding: 0;
  }
  .project-list {
    padding: 10px 10px 10px 10px;
  }
  .about {
    font-size: 3rem;
  }
  .right-container {
    margin-top: 1%;
  }
}
@media only screen and (max-width: 589px) {
  .project-container {
    height: 100vh;
  }
  .hi {
    font-size: 2rem;
  }
  .project-list {
    flex-direction: row;
    flex-wrap: wrap;
    /* background-color: antiquewhite; */
  }

  .project-box-container {
    flex-direction: column;
    flex-wrap: wrap;
    width: 100%;
    height: 70%;
    padding: 20px 20px 20px 20px;
  }
  .list {
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    height: 30%;
    /* justify-content: space-between; */
    /* overflow-x: hidden; */
  }
  .button-54 {
    width: 25%;
    height: 30%;
  }
  .about {
    font-size: 2.7rem;
  }
  .project-heading p {
    font-size: 1.3rem;
  }
  .project-box-bottom ul li {
    padding: 5px 0 5px 0;
    font-size: 0.6rem;
  }
}
@media only screen and (max-width: 570px) {
  .about-me-box-bottom p {
    font-size: 1.1rem;
  }
  .about-container {
    flex-direction: column;
  }
  .about-left {
    width: 100%;
    height: 20%;
    padding-top: 30px;
  }
  .about-right {
    width: 100%;
    height: 70%;
    padding-left: 17px;
    padding-right: 17px;
  }
  .exp-heading-box {
    font-size: 1.5rem;
  }
  .exp-points {
    font-size: 0.9rem;
  }
  .social-media h3 {
    font-size: 2rem;
  }
  .bottomnav .a {
    font-size: 10px !important ;
  }
  .button-54 {
    font-size: 10px;
  }
  .exp-points {
    font-size: 0.7rem !important;
  }
  .exp-heading h2 {
    font-size: 2rem;
  }
}
@media only screen and (max-width: 535px) {
  .hi {
    font-size: 1.4rem;
  }
  .about {
    font-size: 1.8em;
  }
  .about-me-box-bottom {
    height: auto;
  }

  .project-list {
    padding: 0 0 0 0;
  }
}
@media only screen and (max-width: 525px) {
  .about-me-box-bottom p {
    font-size: 0.9rem;
  }
  .skill-heading p {
    font-size: 1.5rem;
  }
  .project-container {
    height: 120vh;
  }
  .project-heading p {
    font-size: 1rem;
  }
  .project-box-bottom ul li {
    padding: 5px 0 5px 0;
    font-size: 0.6rem;
  }

  .col-25 {
    float: left;
    width: 25%;
    margin-top: 3px;
    font-size: 1rem;
  }

  .col-75 {
    float: left;
    width: 75%;
    margin-top: 3px;
  }
  .skill-box{
    padding: 0px 0px 0px 0px;
  }
}
@media only screen and (max-width: 433px) {
  .about {
    font-size: 2.4rem;
  }
  .skill-heading h1 {
    font-size: 3rem;
  }
  .project-heading h1 {
    font-size: 2rem;
    /* padding-bottom: 10px; */
  }
  .exp-heading p {
    font-size: 1.5rem;
  }
  .exp-heading-box {
    font-size: 1rem;
  }
  .exp-points {
    font-size: 0.5rem !important;
  }
  .mail {
    font-size: 1rem;
  }
  .social-media h3 {
    font-size: 1rem;
  }
  .contact-heading {
    font-size: 1.4rem;
  }
  .contact-box2 {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 50px;
  }
 
}
@media only screen and (max-width: 360px) {
    .about{font-size: 2rem;}
}
